.btn-show {
  cursor: pointer;
  position: relative;
  width: 30px;
  transition: 0.5s;
  height: 30px;
}

.btn-show__icon {
  border: none;
  border-radius: 100%;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-color: var(--white);
  cursor: pointer;
  position: relative;
  background-position: center 55%;
  cursor: pointer;
  z-index: 1;
}

.btn-show__icon{
  background-image: url("../../../assets/icons/arrowDownBlue.png");
}

.btn-show::after {
  top: -10%;
  z-index: 0;
  left: -10%;
  position: absolute;
  width: 120%;
  height: 120%;
  border-radius: 100%;
  background: linear-gradient(to right, #577bfa 0%, rgba(217, 217, 217, 0) 70%);
  content: "";
  animation-duration: 3s;
  animation-delay: 0s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes btn-show-show-animation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes btn-show-close-animation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

.btn-show_shown::after {
  animation-name: btn-show-show-animation;
  animation-duration: 0.5s;
  animation-iteration-count: 1;
}

.btn-show_closed::after {
  animation-name: btn-show-close-animation;
  animation-duration: 0.5s;
  animation-iteration-count: 1;
}

.btn-show_closed{
  transform: rotate(180deg);
}

.btn-show__text {
  user-select: none;
}

@media screen and (max-width: 576px) {
  .btn-show {
    width: 24px;
    height: 24px;
  }

  .btn-show__icon{
    background-color: #F3F4F4 !important;
  }
  
}
