.btn_hovered{
  background-color: transparent !important;
  color: var(--blue);
}

.modal {
  width: 100vw;
  height: 100vh;
}

.modal_shown::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  background: var(--black);
  opacity: 0.6;
  z-index: 3;
  display: block;
  transition: 1s;
}

.modal_closed {
  z-index: -100;
  opacity: 0;
}

.modal_shown {
  opacity: 1;
}

.modal__content {
  width: 61.5rem !important;
  height: 28rem;
  padding: 4rem;
  z-index: 4;
  overflow: hidden;
  position: relative;
  border-radius: 50px;
}

.modal__content::after {
  content: "";
  position: absolute;
  opacity: 0.9;
  top: -40%;
  right: -30%;
  z-index: -1;
  width: 600px;
  height: 600px;
  background: radial-gradient(
    50% 50% at 37.3% 50%,
    #577bfa 0%,
    rgba(217, 217, 217, 0) 69.79%
  );
}

/* ///elements */
.modal__close{
  width: 30px;
  height: 30px;
  right: 5%;
  top: 10%;
}

.modal__social-media {
  flex-basis: fit-content;
}

.modal__soc{
  margin-left: 2rem;
}

.modal__soc:nth-child(1){
  margin-left: 0;
}

.modal__soc:nth-child(3){
  margin-left: 2.3rem !important;
}

.modal-title {
  font-size: 3.75rem;
}

@keyframes modal__title_anim {
  0%{
    transform: scaleX(0) scaleY(70%);
  }
  100%{
    transform: scaleX(70%) scaleY(80%);
  }
}

.modal__title_underlined{
  white-space: nowrap;
}

.modal__title_underlined::after{
  content: "";
  position: absolute;
  transform-origin: left;
  animation-name: modal__title_anim;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
  width: 100%;
  background-repeat: no-repeat;
  height: 1rem;
  top: 1em;
  left: 0;
  background-image: url("../../assets/images/modal/underline.svg");
}

.modal__img {
  position: absolute;
  right: 10%;
  height: 24rem;
  bottom: 0;
}

.modal-title {
  line-height: 1.2;
}

.modal__input {
  width: 91% !important;
  height: fit-content !important;
}

.modal__buttons .btn {
  padding: 0.75rem 2rem;
  flex-basis: content;
  width: fit-content !important;
}

.modal__buttons_phone_stage .btn {
  padding: 0.75rem 1rem !important;
}

@media screen and (max-width: 1550px){
  .modal__content::after{
    right: -55%;
  }
}

@media screen and (max-width: 576px) {
  .modal__input {
    width: 100% !important;
    height: fit-content !important;
  }
  

  .modal__buttons .btn {
    width: 100% !important;
  }

  .modal__content {
    padding: 3rem 1.5rem;
    max-width: 80% !important;
    border-radius:30px;
  }

  .modal__offer {
    width: 100%;
  }

  .modal__content::after {
    width: 0;
    height: 0;
  }

  .modal__soc img{
    width: 35px;
  }

  @keyframes modal__title_anim {
    0%{
      transform: scaleX(0) scaleY(70%);
    }
    100%{
      transform: scaleX(40%) scaleY(80%);
    }
  }
}
