.ticket {
  background-color: var(--white);
  border-radius: 60px;
  padding: 3rem 3rem 0rem 3.25rem;
  white-space: nowrap;
  position: relative;
  overflow: hidden;
  line-height: 1.25;
}

.ticket:nth-child(1) {
  padding-bottom: 7.5rem;
  width: 64%;
}

.ticket:nth-child(2) {
  width: 33%;
  margin-left: 1.6rem;
  padding-bottom: 7.5rem;
}

.ticket:nth-child(3) {
  padding-bottom: 4rem;
  margin-top: 1.6rem;
  width: 41%;
}

.ticket:nth-child(4) {
  padding-bottom: 4rem;
  margin-top: 1.675rem;
  width: 56%;
  margin-left: 1.6rem;
}

.ticket__img {
  position: absolute;
  right: 0;
  bottom: 0;
}

.ticket:nth-child(1) .ticket__img{
  transform: translateX(-8%) translateY(-4%);
}

.ticket:nth-child(2) .ticket__img{
  transform: translateX(7%) translateY(30%) ;
}

.ticket:nth-child(3) .ticket__img{
  transform: translateX(15%) scale(100%);
}

.ticket:nth-child(4) .ticket__img{
  max-height: 90%;
  /* transform: translateY(5%) scale(80%); */
}

.ticket__text {
  font-weight: 500 !important;
}

@media screen and (min-width: 1400px) {
  .ticket:nth-child(1) .ticket__img {
    padding-right: 0.5rem;
  }

  .ticket:nth-child(4) .ticket__img {
    padding-right: 3rem;
  }
}

@media screen and (min-width: 1400px) and (max-width: 1550px) {
  .ticket:nth-child(1) .ticket__img {
    max-height: 75%;
  }

  .ticket:nth-child(2) .ticket__img {
    max-width: 80%;
  }

  .ticket:nth-child(3) .ticket__img{
    max-width: 60%;
    max-height: 70%;
  }

  .ticket:nth-child(4) .ticket__img{
    max-height: 90%;
  }
}

@media screen and (max-width: 1200px) and (min-width: 576px) {
  .ticket:nth-child(1),
  .ticket:nth-child(2),
  .ticket:nth-child(3),
  .ticket:nth-child(4) {
    width: 100%;
    margin: 0;
    margin-top: 2rem;
    padding: 3rem 3rem 3rem 3rem;
  }
}

@media screen and (max-width: 576px) {
  .ticket {
    white-space: inherit !important;
    width: 100% !important;
    margin: 0 !important;
    margin-top: 2rem !important;
    padding: 2.5rem 1.5rem 2.5rem 1.5rem !important;
    border-radius: 30px !important;
  }
}
