.btn_close{
  border: none;
  background-color: transparent;
  position: absolute;
  width: 22px;
  height: 22px;
  cursor: pointer;
  border-radius: 100%;
}

.btn_close__icon{
  background-image: url('../../../assets/images/btnClose/cross.svg');
  background-color: transparent;
  background-size: 40%;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  border-radius: 100%;
}

.btn_close::after {
  top: -10%;
  z-index: -1;
  left: -10%;
  position: absolute;
  width: 120%;
  height: 120%;
  background: url("../../../assets/images/btnClose/bg.svg");
  background-size: cover;
  content: "";
  animation-duration: 3s;
  animation-delay: 0s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@media screen and (max-width: 576px) {
  .btn_close{
    top: 8%;
    right: 8%;
    opacity: 0.5;
  }
}

@keyframes anim {
  0%{
      opacity: 0.5;
  }
  100%{
      opacity: 1;
  }
}


