
.underline{
  width: fit-content;
  display: inline-block;
  position: relative;
}

.underline__stroke{
  display: block;
  position: absolute;
  transform-origin: left;
}

.underline__img{
  width: 100%;
  display: block;
}

@keyframes underline_shown {
  0%{
    width: 0%;
  }
  100%{
    width: 100%;
  }
}

@keyframes underline_hidden {
  0%{
    width: 100%;
  }
  100%{
    width: 0%;
  }
}

.underline_hidden{
  animation-name: underline_hidden;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

.underline_shown{
  animation-name: underline_shown;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}