.footer {
  padding-top: 15rem;
  padding-bottom: 15rem;
  width: 100%;
  position: relative;
}

.footer__background {
  opacity: 1;
  content: "";
  z-index: -1;
  position: absolute;
  left: calc(calc(100% - 100vw) / 2);
  bottom: 0;
  width: 100vw;
  background-color: var(--blue);
}

.footer__dash {
  background-color: var(--white);
  width: 1px;
}

.footer hr {
  width: 100%;
  height: 1px !important;
  border-top: 0;
  background-color: var(--white);
}

.footer a {
  color: var(--white) !important;
  text-decoration: none !important;
}

.footer a:hover {
  color: var(--white);
  text-decoration: none !important;
}

@media screen and (max-width: 576px) {
  .footer {
    padding-left: 2.8rem;
    padding-right: 2.8rem;
  }

  .footer__documents {
    margin-top: 4rem;
  }

  .footer__logo {
    width: 100%;
  }
}

@media screen and (max-width: 576px) {
  .footer {
    padding-left: 0;
    padding-right: 0;
  }
}
