.slider-progress .btn-slide-left{
}

.slider-progress__circles{
    margin: 0 2rem 0 2rem;
}

.slider-progress__circle{
    width: 16px;
    height: 16px;
    margin-left: 2rem;
    background-color: var(--grey);
    border-radius: 100%;
}

.slider-progress__circle:first-child{
    margin-left: 0;
}

@keyframes slider-progress__circle_active {
    0%{
    }
    100%{
        transform: scale(140%);
        background-color: var(--blue);
    }
}

.slider-progress__circle_active{
    animation-name: slider-progress__circle_active;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
}

