.btn-slide {
  border: none;
  border-radius: 100%;
  width: 54px;
  height: 54px;
  background-repeat: no-repeat;
  cursor: pointer;
  position: relative;
  background-position: center 55%;
  cursor: pointer;
  z-index: 1;
}

.btn-slide:hover,
.btn-slide:focus {
  border: none !important;
  outline: none !important;
}

.btn-slide__icon {
  top: 0%;
  left: 0%;
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 100%;
  background: var(--light-grey);
  background-repeat: no-repeat;
}

.btn-slide-right .btn-slide__icon {
  background-position: 55% center;
  background-image: url("../../../assets/images/btnSlide/arrowRight.svg");
}

.btn-slide-left .btn-slide__icon {
  background-image: url("../../../assets/images/btnSlide/arrowLeft.svg");
  background-position: 42% center;
}

.btn-slide__border {
  z-index: -1;
  position: absolute;
  border-radius: 100%;
  background: linear-gradient(to right, #577bfa 0%, rgba(217, 217, 217, 0) 70%);
  top: -7%;
  left: -7%;
  width: 114%;
  height: 114%;
}

.btn-slide-left .btn-slide__border{
  transform: rotate(30deg);
}

.btn-slide-right .btn-slide__border{
  transform: rotate(220deg);
}

@keyframes btn-slide-left__border_anim {
  0%{
    transform: rotate(30deg);
  }
  100%{
    transform: rotate(390deg);
  }
}

@keyframes btn-slide-right__border_anim {
  0%{
    transform: rotate(220deg);
  }
  100%{
    transform: rotate(580deg);
  }
}

.btn-slide__border_anim{
  animation-name: btn_slide;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
  animation-timing-function: linear;
}

.btn-slide-left .btn-slide__border_anim{
  animation-name: btn-slide-left__border_anim;
}

.btn-slide-right .btn-slide__border_anim{
  animation-name: btn-slide-right__border_anim;
}

@media screen and (max-width: 992px) {
  .btn-slide {
    width: 60px;
    height: 60px;
  }
}