body {
  /* white-space: nowrap; */
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 500;
  background-color: var(--light-grey);
  line-height: 1.2;
  color: var(--black);
}

#root {
  overflow: hidden;
}

.app {
  position: relative;
  height: fit-content;
  z-index: 1;
}

body::-webkit-scrollbar-thumb{
  width: 0 !important;
  height: 0;
}

a {
  cursor: pointer !important;
}

/* ///app size */

.app {
  width: 1218px;
}

@media screen and (min-width: 1400px) and (max-width: 1550px) {
  .app {
    width: 1000px;
  }
}

@media screen and (min-width: 1200px) and (max-width: 1400px) {
  .app {
    width: 1000px;
  }
}

@media screen and (min-width: 992px) and (max-width: 1200px) {
  .app {
    width: 992px;
  }
}

@media screen and (min-width: 768px) and (max-width: 992px) {
  .app {
    width: 768px;
  }
}

@media screen and (min-width: 576px) and (max-width: 768px) {
  .app {
    width: 576px;
  }
}

@media screen and (max-width: 576px) {
  .app {
    width: 100vw;
  }

  .app {
    padding: 0 2.3rem;
  }
}

@media screen and (max-width: 400px) {
  .app {
    padding: 0 1rem;
  }
}

@media screen and (max-width: 360px) {
  .app {
    padding: 0 1.9rem;
  }
}

/* /// */

/* /// */
/* ///header-margins */
.h-1,
.h-2,
.h-3,
.h-4,
.h-6,
.dialog {
  margin-top: 5rem;
  padding-top: calc(calc(4.5rem + 46px) + 3rem);
  padding-bottom: 3rem;
  margin-bottom: 0;
}

.h-1,
.h-2,
.h-3,
.h-4,
.h-6{
  font-weight: 700;
}

.start-work {
  margin-top: 15rem;
}

.team-stroke{
  width: 103% !important;
  right: -10px;
  bottom: -2px;
}

.h-5 {
  padding-top: calc(calc(4.5rem + 46px) + 3rem);
  margin-top: 12rem;
  margin-bottom: 12rem;
  white-space: nowrap;
  font-weight: 700;
}

.h-6 {
  padding-bottom: 6rem;
}

.h-4 {
  padding-bottom: 7rem;
}

.phone-form__main {
  margin-top: 8.5rem;
}

@media screen and (max-width: 576px) {
  .h-1,
  .h-2,
  .h-3,
  .h-4,
  .h-5,
  .h-6 {
    margin-top: 0;
    padding-bottom: 1.25rem;
  }

  .dialog {
    padding-top: 0;
    margin-top: 9rem;
  }

  .start-work {
    margin-top: 8rem;
  }

  .h-5 {
    margin-bottom: 8rem;
  }

  .h-6 {
    padding-bottom: 3rem;
    white-space: nowrap;
  }

  .h-3,
  .h-5 {
    white-space: nowrap;
  }

  .h-5 {
    margin-left: -5% !important;
    width: 110% !important;
  }

  .phone-form__main {
    margin-top: 3rem;
  }
}

/* ///inputs/buttons */
.input:focus {
  outline: none;
}

.input-phone-wrapper {
  position: relative;
}

/* ///card__btn */
.card__btn {
  border-radius: 30px;
  font-weight: 600;
}
