.chat{
  overflow: hidden;
  height: inherit;
}

.chat__dialog-wrapper{
  height: 87%;
  bottom: 0;
  position: absolute;
  overflow: hidden;
}

@keyframes chat__dialog_anim {
  0%{
    transform: translateY(0%);
  }
  100%{
    transform: translateY(-30%);
  }
}

.chat__dialog{
  animation-name: chat__dialog_anim;
  animation-duration: 5s;
  animation-fill-mode: forwards;
  animation-delay: 3s;

}

@media screen and (max-width: 576px) {
  .chat__bubble {
    position: absolute !important;
    bottom: 2rem;
  }

  .chat__header{
    height: 0.75rem;
  }

  .bubble_right_small {
    right: 1rem;
  }
}
