.confirm-city {
  position: absolute;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  border-top-left-radius: 15px;
  top: 100%;
  transform: translateX(-59%);
  padding: 0.75rem 1.5rem 0.75rem 1.1rem;
}

.confirm-city::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #fafafb;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  border-top-left-radius: 15px;
  z-index: -1;
  opacity: 0.3;
  left: 0;
  top: 0;
}

.confirm-city button {
  border-width: 1px !important;
}

.confirm-city__btn_change {
  border-color: #9a9a9a;
  color: var(--black);
}
