.roadmap {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

@keyframes roadmap__list_animation {
  0% {
    top: -100%;
  }
  100% {
    top: 0;
  }
}

@keyframes roadmap__main_animation {
  0% {
    bottom: -100%;
  }
  100% {
    bottom: 0;
  }
}

.roadmap__list {
  animation-name: roadmap__list_animation;
  animation-duration: 0.5s;
  animation-delay: 0.5s;
  top: -100%;
  animation-fill-mode: forwards;
  z-index: 1;
  width: 62%;
  height: 66%;
  left: 31%;
  position: absolute;
  box-shadow: -5px 5px 5px 1px var(--grey);
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.roadmap__main {
  border-radius: 20px;
  animation-name: roadmap__main_animation;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  bottom: -100%;
  width: 68%;
  height: 85%;
  left: 0%;
  position: absolute;
}

.roadmap__logo{
  width: 45px;
  height: 37px;
}

#roadmap__sign{
  animation-name: sign_animation;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}

.roadmap__sign{
  width: 191px;
  height: 61px;
  background-size: 100%;
  margin-left: -10px !important;
  background-repeat: no-repeat;
  background-image: url("../../../assets/images/roadmap/sign.svg");
}

.roadmap__name{
  width: fit-content;
}

.roadmap__name{
  color: var(--blue);
  white-space: nowrap;
}

@keyframes roadmap__text_animation {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
    color: var(--blue);
  }
  100% {
    opacity: 1;
    color: var(--black);
  }
}

@keyframes becomeInvisible {
  0% {
    opacity: 1;
  }
  25% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
  75% {
    opacity: 0.5;
  }
  99% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes roadmap__circle_border_animation {
  0% {
    opacity: 0;
    transform: rotate(0);
    border-top-color: var(--blue);
    border-right-color: var(--blue);
  }
  100% {
    opacity: 1;
    border-left-color: var(--blue);
    transform: rotate(360deg);
    border-bottom-color: var(--blue);
    border-top-color: var(--blue);
    border-right-color: var(--blue);
  }
}

@keyframes roadmap__circle_arrow_animation {
  0% {
    background-color: var(--grey);
    opacity: 0;
  }
  100% {
    opacity: 1;
    background-color: transparent;
  }
}

@keyframes roadmap__key_animation {
  0% {
    height: 0;
  }
  100% {
    height: 1.1rem;
  }
}

.roadmap__step {
  margin-top: 1.1rem;
  position: relative;
}

.roadmap__step::after {
  animation-name: roadmap__key_animation;
  animation-duration: 2s;
  animation-delay: 2s;
  animation-fill-mode: forwards;
  content: "";
  position: absolute;
  width: 2px;
  height: 0;
  background-color: var(--blue);
  left: 15px;
  top: 100%;
}

.roadmap__circle {
  flex-basis: 32px;
  height: 32px;
  position: relative;
}

.roadmap__circle::after {
  animation-name: roadmap__circle_border_animation;
  animation-duration: 2s;
  animation-fill-mode: forwards;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border-radius: 100%;
  border-left-color: transparent;
  border-bottom-color: transparent;
  border: 2px solid transparent;
}

.roadmap__circle::before {
  animation-name: roadmap__circle_arrow_animation;
  animation-duration: 6000ms;
  animation-fill-mode: forwards;
  content: "";
  position: absolute;
  width: calc(100% - 3px);
  height: calc(100% - 3px);
  left: 1.5px;
  top: 1.5px;
  border-radius: 100%;
  opacity: 0;
  background-image: url("../../../assets/images/roadmap/arrowDown.svg");
  background-repeat: no-repeat;
  background-position: center 54%;
}


.roadmap__text{
  animation-name: roadmap__text_animation;
  animation-duration: 2s;
  color: transparent;
  animation-fill-mode: forwards;
  position: absolute;
  width: 100%;
  flex-basis: calc(100% - 42px);
  left: 42px;
  top: calc(50% - 0.6em);
  content: "";
}

.roadmap__step_1 .roadmap__text{
  top: calc(50% - 1em) !important;
}

@media screen and (max-width: 576px) {
  @keyframes roadmap__main_animation {
    0% {
      top: 10%;
    }
    100% {
      top: -110%;
    }
  }

  .roadmap__circle{
    width: 25px;
    height: 25px;
    flex-basis: 25px;
  }

  .roadmap__step::after {
    left: 12px;
  }

  .roadmap__circle::before {
    background-size: 50%;
  }

  .roadmap__text{
    left: 35px;
  }
  
  .roadmap__sign{
    width: 50%;
    position: absolute;
    right: 10%;
    bottom: 0%;
    height: 44px !important;
  }

  .roadmap__list{
    box-shadow: 0px 0px 10px 1px var(--grey);
    right: 3%;
    width: 65%;
    height: 75%;
  }

  .roadmap__main{
    top: 10%;
    animation-name: roadmap__main_animation;
    animation-timing-function: linear;
    animation-duration: 20s;
    animation-delay: 1s;
    animation-fill-mode: forwards;
    left: 3%;
    height: 300%;
    width: 65%;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
  }

  .roadmap__logo{
    width: 36px;
    height: 29px;
  }
}
