.barcode {
  transform: rotate(1deg);
}

.check {
  position: relative;
  overflow: hidden;
  height: 100%;
}

@keyframes check__list_animation {
  0% {
    top: -100%;
  }
  100% {
    top: -20%;
  }
}

.check__list {
  animation-name: check__list_animation;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  border-radius: 30px;
  position: absolute;
  width: 67%;
  height: 110%;
  right: 15%;
  transform: rotate(10deg);
}

@media screen and (min-width: 996px) and (max-width: 1200px) {
  .check__list {
    width: 90%;
    left: 10%;
  }
}

.check__text {
  width: calc(100% - 36px);
  /* margin-left: 18px; */
}

.check__circle,
.check__text {
  position: relative;
}

@keyframes check_stroke_anim {
  0%{
    scale: 0 100%;
  }
  100%{
    scale: 100% 100%;
  }
}

.check__text_0::before,
.check__text_1::before,
.check__text_2::before,
.check__text_3::before,
.check__text_4::before,
.check__text_5::before,
.check__text_6::before,
.check__text_7::before {
  animation-name: check_stroke_anim;
  transform-origin: left;
  animation-fill-mode: forwards;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  content: "";
  position: absolute;
  background-size: contain;
  scale: 0 100%;
  width: 100%;
  background-repeat: no-repeat;
}

.check__point_4,
.check__point_7 {
  margin-top: 0 !important;
}

.check__text_0::before {
  transform: translateY(30%) rotate(-10deg);
  height: 23px;
  background-image: url("../../../assets/images/check/string1.svg");
}
.check__text_3::before {
  transform: translateY(30%) rotate(-10deg);
  height: 30px;
  background-image: url("../../../assets/images/check/string2.svg");
}

.check__text_4::before {
  transform: translateY(50%) rotate(-10deg);
  height: 20px;
  background-image: url("../../../assets/images/check/string3.svg");
}

.check__text_6::before {
  transform: translateY(25%) rotate(-10deg);
  height: 30px;
  background-image: url("../../../assets/images/check/string4.svg");
}

.check__text_7::before {
  transform: translateY(35%) rotate(-10deg);
  height: 29px;
  background-image: url("../../../assets/images/check/string5.svg");
}

/* //// */
.check__circle_0::before {
  background-image: url("../../../assets/images/check/mark1.svg");
}

.check__circle_1::before {
  background-image: url("../../../assets/images/check/mark2.svg");
}

.check__circle_2::before {
  background-image: url("../../../assets/images/check/mark3.svg");
}

.check__circle_3::before {
  background-image: url("../../../assets/images/check/mark4.svg");
}

.check__circle_4::before {
  left: 5px;
  background-image: url("../../../assets/images/check/mark5.svg");
}

.check__circle_5::before {
  left: 5px;
  background-image: url("../../../assets/images/check/mark6.svg");
}

@media screen and (max-width: 576px) {
  @keyframes check__list_move {
    0% {
      transform: rotate(10deg) translateY(5%);
    }
    100% {
      transform: rotate(10deg) translateY(-35%);
    }
  }

  .check__list {
    transform: rotate(10deg);
    animation-name: check__list_move;
    animation-duration: 8s;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
    border-radius: 30px;
    position: absolute;
    width: 100%;
    height: 400% !important;
    left: -5%;
    top: -25%;
    transform: rotate(10deg);
  }

  .check__list {
    height: 200%;
    top: 5% !important;
  }
}
