@keyframes width_animation_2 {
  0% {
    transform: scaleX(0%)scaleY(100%) ;
  }
  100% {
    transform: scaleX(100%) scaleY(100%);
  }
}

@keyframes width_animation_3 {
  0% {
    transform: scaleX(0%)scaleY(100%) ;
  }
  100% {
    transform: scaleX(115%) scaleY(100%);
  }
}

.stop-animation div, .stop-animation .switch__btn_active .switch__rect_anim{
  animation-name: none;
}

.how-we-work__title::after {
  content: "";
  background-image: url("../../assets/images/underlines/hww.svg");
  background-size: contain;
  height: 25px;
  position: absolute;
  background-repeat: no-repeat;
  animation-fill-mode: forwards;
  transform: scaleX(0);
  transform-origin: left;
  animation-duration: 0.5s;
  animation-delay: 1s;
  animation-timing-function: linear;
}

.how-we-work__block:nth-child(1) .how-we-work__title::after {
  animation-name: width_animation_2;
  width: 56%;
  left: -5px;
  bottom: -23px;
}

.how-we-work__block:nth-child(2) .how-we-work__title::after {
  animation-name: width_animation_2;
  width: 50%;
  top: 1.1em;
  right: -9%;
}

.how-we-work__block:nth-child(3) .how-we-work__title::after {
  animation-name: width_animation_3;
  width: 59%;
  top: 1.1em;
  left: -1%;
}

.how-we-work__title {
  white-space: nowrap;
  position: relative;
}

.how-we-work__annotation{
  white-space: nowrap;
}

@media (min-width: 1200px)and (max-width: 1601px) {
  /* .col-xl-7 {
    flex: 0 0 52% !important;
    max-width: 54%;
  }

  .col-xl-6 {
    flex: 0 0 40%;
    max-width: 40%;
  } */

  .how-we-work__body{
    margin: 1rem auto !important;
    justify-content: center !important;
  }
}


@media screen and (max-width: 992px) {
  .how-we-work__header::after {
    top: 1.1em;
    bottom: unset;
  }
}

.hwwWidget {
  height: 30rem;
  border-radius: 20px;
}

@keyframes fadeout {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.how-we-work__block {
  animation-name: fadeout;
  animation-duration: 1s;
  animation-timing-function: ease-in;
}

/* ///check */
.hwwWidget {
  width: 90%;
}

/* ///roadmap */

@media screen and (max-width: 576px) {
  .hwwWidget {
    width: 100%;
    height: 11rem;
  }

  .how-we-work__block:nth-child(1) .how-we-work__title::after {
    width: 50%;
    left: 0;
  }
  
  .how-we-work__block:nth-child(2) .how-we-work__title::after {
    /* max-width: 10rem; */
    left: -1%;
    top: 2.3em;
    width: 40%;
  }
  
  .how-we-work__block:nth-child(3) .how-we-work__title::after {
    top: 1.1em;
    width: 52%;
    left: 1%;
  }
}

