.banner__title {
  font-size: 3.65rem;
}

.text-xxxl {
  font-size: 2.5rem;
}

.text-xxl {
  font-size: 2.375rem;
}

.text-xl {
  font-size: 2.25rem;
}

.text-l {
  font-size: 2rem;
}

.text-m {
  font-size: 1.5rem;
}

.text-s {
  font-size: 1.375rem;
}

.text-xs {
  font-size: 1.25rem;
}

.text-xxs {
  font-size: 1.125rem;
}

.text-xxxs {
  font-size: 1rem;
}

.bolder {
  font-weight: 700;
}

.thin {
  font-weight: 400;
}

h1,
h2 {
  font-style: normal;
  font-weight: 600;
  text-align: center;
  font-family: "Montserrat";
}

h1 {
  font-size: 5.3rem;
}

h2 {
  font-size: 3.75rem;
}

@media screen and (max-width: 1550px) {
  html {
    font-size: 0.9vw;
  }
}

@media screen and (max-width: 1200px) {
}

@media screen and (max-width: 992px) {
}

@media screen and (max-width: 768px) {
}

@media screen and (max-width: 576px) and (min-width: 420px) {
  html {
    font-size: 16px;
  }


  .select__content{
    font-size: 1rem;
    color: var(--blue);
  }

  .select__active{
    border-color: var(--blue);
  }

  .select__els{
    border-color: var(--blue);
  }

  /* ////// */

  .btn{
    font-size: 1.15rem !important;
  }

  .phone-form__input {
    font-size: 1.15rem !important;
  }

  /* ////// */
  /* //points */
  .point__header{
    font-size: 2.25rem;
    font-weight: 500 !important;

  }

  .point__annotation{
    font-size: 1.15rem;
    font-weight: 500 !important;
  }
  /* //how-we-work */
  .how-we-work__title{
    font-size: 1.5rem !important;
  }

  .how-we-work__annotation{
    font-size: 1.25rem !important;
    font-weight: 500 !important;
  }


  /* //check */
  .check__point{
    font-size: 0.875rem !important;
  }

  /* //roadmap */
  .roadmap__title{
    font-size: 0.875rem !important;
  }

  .roadmap__text{
    font-size: 0.75rem !important;
  }

  .roadmap__signs{
    font-size: 0.75rem !important;
  }

  .roadmap__annotation{
    font-size: 0.675rem !important;
  }

  /* //banner */
  .banner {
    white-space: nowrap;
  }

  .banner__title {
    margin-left: -10% !important;
    width: 120%;
    font-size: 2.25rem;
  }

  .banner__annotation {
    margin-left: -10% !important;
    width: 120%;
    font-size: 1.15rem !important;
  }

  /* //tickets */
  .ticket__header{
    font-size: 1.5rem !important;
  }

  .ticket__text{
    font-size: 1.25rem !important;
  }

  /* //modal */
  .modal__title_underlined{
    font-size: 1.75rem !important;
  }

  .modal-title{
    font-size: 1.75rem !important;
  }

  .modal__annotation{
    font-size: 1rem !important;
  }

  /* //switch */
  .switch-small__btn {
    font-weight: 600;
    font-size: 1rem;
  }

  /* //tabs */
  .tab__header {
    white-space: nowrap !important;
    font-size: 1.5rem !important;
  }

  .tab__text {
    white-space: nowrap !important;
    font-size: 1.375rem !important;
    font-weight: 500 !important;
  }

  /* //dialog */
  .bubble__body {
    white-space: nowrap;
  }

  .dialog__bubble_left, .dialog__bubble_right{
    font-size: 1rem;
  } 

  .chat__bubble{
    font-size: 0.875rem !important;
  }

  /* //slider */
  

  .slider-small__text {
    font-weight: 500;
    font-size: 1.25rem !important;
  }

  .we-coped__title {
    font-weight: 500;
    white-space: nowrap;
    font-size: 1.5rem !important;
  }

  .we-coped__annotation {
    font-weight: 500;
    font-size: 1.25rem !important;
  }

  /* //footer */
  .footer__documents a {
    font-size: 0.8rem !important;
  }

  .footer__phone{
    font-size: 1.75rem;
  }

  .footer__mail{
    font-size: 1.75rem;
  }

  /* //cookie */
  .cookie__text{
    font-size: 1rem !important;
  }

  .cookie__learn-more{
    font-size: 1rem !important;
  }

  .cookie .btn{
    font-size: 1rem !important;
  }

  /* //cards */
  .card__header{
    font-size: 1.75rem !important;
  }

  .card__text{
    font-weight: 500 !important;
    font-size: 1.25rem !important;
  }

  .card__annotation{
    font-weight: 500 !important;
  }

  .btn-show ~ div{
    font-size: 1.125rem;
  }

  h1 {
    font-size: 5.3rem;
  }

  h2 {
    font-size: 2.25rem;
  }

  .start-work__title {
    font-size: 2.5rem !important;
  }

  .start-work__annotation {
    font-weight: 500!important;
  }
}

