.disclosure{
    position: relative;
    height: 100%;
    max-height: 100%;
    flex-grow: 0;
    flex-shrink: 0;
}

.disclosure__gradient {
    opacity: 1;
    content: "";
    z-index: -10;
    position: absolute;
    left: calc(calc(100% - 100vw) / 2);
    top: 0;
    width: 100vw;
    background: linear-gradient(
      360deg,
      #577bfa 20%,
      rgba(164, 219, 248, 0) 100%
    );
  }

  .disclosure__solid {
    opacity: 1;
    content: "";
    z-index: -10;
    position: absolute;
    left: calc(calc(100% - 100vw) / 2);
    width: 100vw;
    background: var(--blue)
  }
  
  
  