.card {
  transition: height 1s;
  border: none;
  cursor: pointer;
}

.card__header {
  min-height: 3.5em;
}

.card__header {
  white-space: nowrap;
  font-size: 32px;
}

.card__text {
  white-space: nowrap;
  min-height: 3em;
}

.card__annotation_shown {
  white-space: nowrap;
  min-height: 11em;
}

.card__price {
  font-size: 3.125rem;
}

@keyframes anim {
  0% {
    opacity: 0;
    transform: translateY(-20%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

@keyframes anim1 {
  0% {
  min-height: 11em;
  opacity: 1;
    transform: translateY(0%);
  }
  50% {
    opacity: 0;
    transform: translateY(-10%);
  }
  100%{
    height: 0;
    width: 0;
  }
}

.card__annotation_closed {
  animation-name: anim1;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}

.card__annotation_shown {
  animation-name: anim;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

@media screen and (max-width: 576px) {
  .card {
    font-size: 0.6rem;
  }

  
}
