.burger {
  width: 20px;
  height: 14px;
  display: flex;
  justify-content: space-between;
  flex-flow: column;
  position: relative;
}

.burger::after{
  content: "";
  background-image: url('../../../assets/images/burger/border.svg');
  width: 210%;
  height: 300%;
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  top: -100%;
  left: -53%;
}

.burger__string {
  height: 2px;
  width: 100%;
  background-color: var(--blue);
}

.burger__string {
  transition: 0.25s;
}

@keyframes rotate {
  100%{
    transform: rotate(180deg);
  }
}

@keyframes rotateBack {
  0%{
    transform: rotate(180deg);
  }
  100%{
    transform: rotate(0deg);
  }
}

.burger__opened::after{
  animation-name:rotateBack;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

.burger__closed::after{
  animation-name:rotate;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

.burger__closed > .burger__string:nth-child(1) {
  transform-origin: left;
  transform: translateX(3px) translateY(-1px) rotate(45deg) translateX(-1px);
}

.burger__closed > .burger__string:nth-child(2) {
  opacity: 0;
}

.burger__closed > .burger__string:nth-child(3) {
  transform-origin: left;
  transform: translateX(3px) translateY(1px) rotate(-45deg);
}

@media screen and (max-width: 576px) {
  .burger {
    width: 18px;
    height: 12px;
    display: flex;
    justify-content: space-between;
    flex-flow: column;
    position: relative;
  }
  
  .burger::after{
    content: "";
    background-image: url('../../../assets/images/burger/border.svg');
    width: 200%;
    height: 300%;
    background-size: cover;
    background-repeat: no-repeat;
    position: absolute;
    top: -100%;
    left: -53%;
  }
  
  .burger__string {
    height: 2px;
    width: 100%;
    background-color: var(--blue);
  }
  
}