.select {
  appearance: none;
  background: transparent;
  cursor: pointer;
  height: fit-content;
  position: relative;
  width: 9em;
  height: fit-content;
}

.select__active{
  text-align: left;
  border-radius: 30px;
  padding: 0.5rem 1.5rem 0.5rem 1.1rem;
  color: var(--blue);
  left: 0;
  width: 100%;
  height: fit-content;
  border: 2px solid var(--blue);
}

.select__active_shown{
  border-radius: 30px;
  left: 0;
  width: 100%;
  height: fit-content;
  border-bottom-color: transparent !important;
  color: var(--black);
  border-top-left-radius: 23px;
  border-top-right-radius: 23px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.select__els {
  text-align: left;
  position: absolute;
  top: calc(100% - 2px);
  left: 0;
  width: 100%;
  border: 2px solid var(--blue);
  border-top: 0 !important;
  border-bottom-left-radius: 23px;
  border-bottom-right-radius: 23px;
}

.select__els_hidden {
  display: none;
}

.select__el {
  transition: 0.25s;
  position: relative;
  padding: 0.5rem 1.5rem 0.5rem 1.1rem;
}

.select__el:hover, .select__active_shown:hover {
  color: var(--blue);
}

.select__btn-show{
  width: 20px !important;
  height: 20px !important;
}

.select__btn-show>.btn-show__icon{
  background-size: 60%;
}

@media screen and (max-width: 576px) {
  .select {
    width: 7.5em;
  }

  .select__btn-show>.btn-show__icon{
    background-size: 60%;
  }

  .select__active{
    padding: 0.5rem 1rem 0.5rem 1.1rem;
  }
}


